// Ant table
.ant-table {
  margin-bottom: 25px !important;
}

// Ant layout
.ant-layout {
  background-color: transparent;
}

// Ant Input
.ant-input,
input {
  border-radius: 4px !important;
  padding: 5px !important;
  border: 1px solid rgb(217, 217, 217);
}

// Form form item
.ant-form-item {
  margin-bottom: 10px !important;
}

// Ant form label
.ant-form-item-label {
  line-height: normal;
}

// Ant Switch (checked)
.ant-switch-checked {
  background-color: #d04283;
}

// Ant Switch loading animation
.ant-switch-loading-icon {
  color: gray !important;
}

// Ant Login progress bar
.ant-steps-item-finish .ant-steps-item-icon > .ant-steps-icon,
.ant-input-group-addon .ant-select-open .ant-select-selection,
.ant-input-group-addon .ant-select-focused .ant-select-selection,
.has-error .ant-form-explain,
.has-error .ant-form-split {
  color: #d04283;
}

// Ant Login progress bar
.ant-steps-item-finish .ant-steps-item-icon > .ant-steps-icon .ant-steps-icon-dot,
.ant-steps-item-process .ant-steps-item-icon > .ant-steps-icon .ant-steps-icon-dot {
  background: #d04283;
}

// Ant Login progress bar
.ant-steps-item-finish > .ant-steps-item-container > .ant-steps-item-tail::after {
  background-color: #d04283;
}

.ant-dropdown-menu-item:hover,
.ant-dropdown-menu-submenu-title:hover,
.ant-dropdown-menu-item-selected,
.ant-dropdown-menu-submenu-title-selected,
.ant-dropdown-menu-item-selected > a,
.ant-dropdown-menu-submenu-title-selected > a {
  background-color: #d0428220 !important;
  color: #d04283;
}

// Ant overrides
.ant-input:focus,
.ant-input:hover,
.ant-switch-checked {
  border-color: #d04282ea !important;
  box-shadow: none;
}

// Ant button
.ant-btn:active,
.ant-btn.active,
.ant-btn:hover,
.ant-btn:focus {
  color: #d04283 !important;
  border-color: #d04283 !important;
}

// Ant Primary button
.ant-btn-primary {
  background-color: #d04283 !important;
  border-color: #d04283 !important;

  &:hover {
    opacity: 0.8;
    color: #fff !important;
  }

  &:focus {
    color: #fff !important;
  }
}

// Ant Slider normal
.ant-slider-track {
  background-color: #d04283ae;
}

// Ant Slider hover
.ant-slider:hover .ant-slider-track {
  background-color: #d04283;
}

// Ant Slider handle normal
.ant-slider-handle,
.ant-slider:hover .ant-slider-handle:not(.ant-tooltip-open) {
  border-color: #d04283ae;
}

// Ant Slider handle boxshadow
.ant-slider-handle:focus {
  box-shadow: 0 0 0 5px #d0428220;
}

// Ant Slider handle active
.ant-slider-handle.ant-tooltip-open,
.ant-slider-handle:focus {
  border-color: #d04283;
}

// Ant Loading spin dots
.ant-spin-dot-item {
  background-color: #d04283;
}

.ant-menu-horizontal > .ant-menu-item-active,
.ant-menu-horizontal > .ant-menu-submenu-active,
.ant-menu-horizontal > .ant-menu-item-open,
.ant-menu-horizontal > .ant-menu-submenu-open,
.ant-menu-horizontal > .ant-menu-item-selected,
.ant-menu-horizontal > .ant-menu-submenu-selected {
  border-color: #d04283 !important;
  color: #d04283 !important;
}

.ant-menu-horizontal > .ant-menu-item:hover,
.ant-menu-horizontal > .ant-menu-submenu:hover {
  border-color: #d04283ae !important;
  color: #d04283ae !important;
}
