@import "./common/styles/responsive";
@import "./common/styles/ant-override.scss";

.loading-app {
  display: flex;
  width: 100vw;
  height: 100vh;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;

  .loading-app-text {
    font-size: 24px;
    margin-bottom: 20px;
    font-size: bold;
  }
}

.explore_container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.contentWrapper {
  display: flex;
  flex: 1;
}

.player-visible {
  margin-bottom: 90px; // Adds some extra view space so smaller screens can see the page bottom
}

AppFooter {
  display: flex;
  flex: 1;
}

.flexRow {
  display: flex;
  flex-direction: row;
}

.flexCol {
  display: flex;
  flex-direction: column;
}

// Global Marquee styling
.marquee {
  /* Starting position */
  -moz-transform: translateX(0%);
  -webkit-transform: translateX(0%);
  transform: translateX(0%);

  /* Apply animation to this element */
  -moz-animation: textSlideLeft 15s linear infinite;
  -webkit-animation: textSlideLeft 15s linear infinite;
  animation: textSlideLeft 15s linear infinite;
}

/* Slide Left animation */
@keyframes textSlideLeft {
  0% {
    -moz-transform: translateX(0%); /* Firefox bug fix */
    -webkit-transform: translateX(0%); /* Firefox bug fix */
    transform: translateX(0%);
  }
  30% {
    -moz-transform: translateX(0%); /* Firefox bug fix */
    -webkit-transform: translateX(0%); /* Firefox bug fix */
    transform: translateX(0%);
  }
  100% {
    -moz-transform: translateX(-100%); /* Firefox bug fix */
    -webkit-transform: translateX(-100%); /* Firefox bug fix */
    transform: translateX(-100%);
  }
}
